import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import LinearProgress from '@material-ui/core/LinearProgress';
import Hidden from '@material-ui/core/Hidden';

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 10,
    borderRadius: 5,
  },
  colorPrimary: {
    backgroundColor: theme.palette.grey[theme.palette.type === 'light' ? 200 : 700],
  },
  bar: {
    borderRadius: 5,
    backgroundColor: '#1a90ff',
  },
}))(LinearProgress);

// Inspired by the former Facebook spinners.
const useStylesFacebook = makeStyles((theme) => ({
  root: {
    position: 'relative',
  },
  bottom: {
    color: theme.palette.grey[theme.palette.type === 'light' ? 200 : 700],
  },
  top: {
    color: '#1a90ff',
    animationDuration: '550ms',
    position: 'absolute',
    left: 0,
  },
  circle: {
    strokeLinecap: 'round',
  },
}));

function FacebookCircularProgress(props) {
  const classes = useStylesFacebook();

  return (
    <div className={classes.root}>
      {/* <CircularProgress
        variant="determinate"
        className={classes.bottom}
        size={40}
        thickness={4}
        {...props}
        value={100}
      /> */}
      <CircularProgress
        variant="indeterminate"
        disableShrink
        className={classes.top}
        classes={{
          circle: classes.circle,
        }}
        size={40}
        thickness={4}
        {...props}
      />
    </div>
  );
}

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    backgroundColor: '#333',
    width: '100%',
    height: '100vh',
  },
  separador: {
    paddingTop: '17rem',
  },
  circulo: {
    marginRight: '20rem',
    marginLeft: '38.2rem',
    display: 'flex',
    justifyContent: 'center',
  },
  linea: {
    marginRight: '20rem',
    marginLeft: '20rem',
  },

  rootCel: {
    flexGrow: 1,
    backgroundColor: '#333',
    width: '100%',
    height: '100vh',
  },
  separadorCel: {
    paddingTop: '10rem',
  },
  circuloCel: {
    // marginRight: '20rem',
    marginLeft: '8.4rem',
    display: 'flex',
    justifyContent: 'center',
  },
  lineaCel: {
    marginRight: '3rem',
    marginLeft: '3rem',
  },
});

export default function CustomizedProgressBars() {
  const classes = useStyles();

  return (
    <section>
      <Hidden only={['xs', 'sm']}>
        <div className={classes.root}>
          <section className={classes.separador} />
          <FacebookCircularProgress className={classes.circulo} />
          <br />
          <BorderLinearProgress className={classes.linea} variant="determinate" value={50} />
        </div>
      </Hidden>

      {/* Pantalla adaptada a Celular XS */}
      <Hidden only={['sm', 'md', 'lg', 'xl']}>
          <div className={classes.rootCel}>
            <section className={classes.separadorCel} />
            <FacebookCircularProgress className={classes.circuloCel} />
            <br />
            <BorderLinearProgress className={classes.lineaCel} variant="determinate" value={50} />
        </div>
      </Hidden>
    </section>
  );
}
